$cell-size: 100px;
$cell-gap: 1px;
$calendar-padding: 10px;

$week:
  "mon" 0,
  "tue" $cell-size + $cell-gap,
  "wed" ($cell-size + $cell-gap) * 2,
  "thu" ($cell-size + $cell-gap) * 3,
  "fri" ($cell-size + $cell-gap) * 4,
  "sat" ($cell-size + $cell-gap) * 5,
  "sun" ($cell-size + $cell-gap) * 6;
